import card from './Character_Fischl_Card.jpg'
import thumb from './Character_Fischl_Thumb.png'
import c1 from './Constellation_Gaze_of_the_Deep.png'
import c2 from './Constellation_Devourer_of_All_Sins.png'
import c3 from './Constellation_Wings_of_Nightmare.png'
import c4 from './Constellation_Her_Pilgrimage_of_Bleak.png'
import c5 from './Constellation_Against_the_Fleeing_Light.png'
import c6 from './Constellation_Evernight_Raven.png'
import normal from './Talent_Bolts_of_Downfall.png'
import skill from './Talent_Nightrider.png'
import burst from './Talent_Midnight_Phantasmagoria.png'
import passive1 from './Talent_Stellar_Predator.png'
import passive2 from './Talent_Lightning_Smite.png'
import passive3 from './Talent_Mein_Hausgarten.png'
import Stat from '../../../Stat'
import formula, { data } from './data'
import data_gen from './data_gen.json'
import { getTalentStatKey, getTalentStatKeyVariant } from '../../../Build/Build'
import { ICharacterSheet } from '../../../Types/character'
import { IConditionals } from '../../../Types/IConditional'
const conditionals: IConditionals = {}

const char: ICharacterSheet = {
  name: "Fischl",
  cardImg: card,
  thumbImg: thumb,
  star: 4,
  elementKey: "electro",
  weaponTypeKey: "bow",
  gender: "F",
  constellationName: "Corvus",
  titles: ["Prinzessin der Verurteilung", "Sovereign of Immernachtreich", "Ruler of the Ashen Darkness"],
  baseStat: data_gen.base,
  baseStatCurve: data_gen.curves,
  ascensions: data_gen.ascensions,
  talent: {
    formula,
    conditionals,
    sheets: {
      auto: {
        name: "Bolts of Downfall",
        img: normal,
        sections: [{
          text: <span><strong>Normal Attack</strong> Perform up to 5 consecutive shots with a bow.</span>,
          fields: data.normal.hitArr.map((percentArr, i) =>
          ({
            text: `${i + 1}-Hit DMG`,
            formulaText: stats => <span>{percentArr[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("normal", stats), stats)}</span>,
            formula: formula.normal[i],
            variant: stats => getTalentStatKeyVariant("normal", stats),
          }))
        }, {
          text: <span><strong>Charged Attack</strong> Perform a more precise Aimed Shot with increased DMG. While aiming, the dark lightning spirits of Immernachtreich shall heed the call of their Prinzessin and indwell the enchanted arrowhead. When fully indwelt, the RachsÃ¼chtig Blitz shall deal immense <span className="text-electro">Electro DMG</span>.</span>,
          fields: [{
            text: `Aimed Shot DMG`,
            formulaText: stats => <span>{data.charged.aimedShot[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("charged", stats), stats)}</span>,
            formula: formula.charged.aimShot,
            variant: stats => getTalentStatKeyVariant("charged", stats),
          }, {
            text: `Fully-Charged Aimed Shot DMG`,
            formulaText: stats => <span>{data.charged.fullAimedShot[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("charged", stats, "electro"), stats)}</span>,
            formula: formula.charged.fullAimedShot,
            variant: stats => getTalentStatKeyVariant("charged", stats, "electro"),
          }, {
            canShow: stats => stats.ascension >= 1,
            text: <span>Full Aimed Shot on Oz <span className="text-electro">AoE</span></span>,
            formulaText: stats => <span>152.7% * {data.charged.fullAimedShot[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("charged", stats, "electro"), stats)}</span>,
            formula: formula.charged.fullAimedShotOz,
            variant: stats => getTalentStatKeyVariant("charged", stats, "electro"),
          }]
        }, {
          text: <span><strong>Plunging Attack</strong> Fires off a shower of arrows in mid-air before falling and striking the ground, dealing AoE DMG upon impact.</span>,
          fields: [{
            text: `Plunge DMG`,
            formulaText: stats => <span>{data.plunging.dmg[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("plunging", stats), stats)}</span>,
            formula: formula.plunging.dmg,
            variant: stats => getTalentStatKeyVariant("plunging", stats),
          }, {
            text: `Low Plunge DMG`,
            formulaText: stats => <span>{data.plunging.low[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("plunging", stats), stats)}</span>,
            formula: formula.plunging.low,
            variant: stats => getTalentStatKeyVariant("plunging", stats),
          }, {
            text: `High Plunge DMG`,
            formulaText: stats => <span>{data.plunging.high[stats.tlvl.auto]}% {Stat.printStat(getTalentStatKey("plunging", stats), stats)}</span>,
            formula: formula.plunging.high,
            variant: stats => getTalentStatKeyVariant("plunging", stats),
          }]
        }],
      },
      skill: {
        name: "Nightrider",
        img: skill,
        sections: [{
          text: <span>
            <p className="mb-2">Summons Oz. The night raven forged of darkness and lightning descends upon the land, dealing <span className="text-electro">Electro DMG</span> in a small AoE.</p>
            <p className="mb-0">For the ability's duration, Oz will continuously attack nearby opponents with <span className="text-electro">Freikugel</span>.</p>
            <p className="mb-0">Hold to adjust the location Oz will be summoned to.</p>
            <p className="mb-0">Press again any time during the ability's duration to once again summon him to Fischl's side.</p>
          </span>,
          fields: [{
            text: "Oz's ATK DMG",
            formulaText: stats => <span>{data.skill.oz[stats.tlvl.skill]}% {Stat.printStat(getTalentStatKey("skill", stats), stats)}</span>,
            formula: formula.skill.oz,
            variant: stats => getTalentStatKeyVariant("skill", stats),
          }, {
            text: "Summoning DMG",
            formulaText: stats => <span>{data.skill.dmg[stats.tlvl.skill]}%{stats.constellation >= 2 ? " + 200%" : ""} {Stat.printStat(getTalentStatKey("skill", stats), stats)}</span>,
            formula: formula.skill.dmg,
            variant: stats => getTalentStatKeyVariant("skill", stats),
          }, {
            canShow: stats => stats.constellation >= 6,
            text: "Attack with Active Character",
            formulaText: stats => <span>30% {Stat.printStat(getTalentStatKey("skill", stats), stats)}</span>,
            formula: formula.skill.activeChar,
            variant: stats => getTalentStatKeyVariant("skill", stats),
          }, {
            text: "Duration",
            value: stats => stats.constellation < 6 ? "10s" : "10s + 2s",
          }, {
            text: "CD",
            value: "25s",
          }, {
            canShow: stats => stats.constellation >= 2,
            text: "AoE Increase",
            value: "50%",
          }]
        }],
      },
      burst: {
        name: "Midnight Phantasmagoria",
        img: burst,
        sections: [{
          text: <span>
            <p className="mb-2">Summons Oz to spread his twin wings of twilight and defend Fischl. Has the following properties during the ability's duration:</p>
            <ul>
              <li>Fischl takes on Oz's form, greatly increasing her Movement Speed.</li>
              <li>Strikes nearby opponents with lightning, dealing <span className="text-electro">Electro DMG</span> to opponents she comes in contact with. Each opponent can only be struck once.</li>
              <li>Once this ability's effects end, Oz will remain on the battlefield and attack his Prinzessin's foes. If Oz is already on the field, then this will reset the duration of his presence.</li>
            </ul>
          </span>,
          fields: [{
            text: "Falling Thunder DMG",
            formulaText: stats => <span>{data.burst.dmg[stats.tlvl.burst]}% {Stat.printStat(getTalentStatKey("burst", stats), stats)}</span>,
            formula: formula.burst.dmg,
            variant: stats => getTalentStatKeyVariant("burst", stats),
          }, {
            canShow: stats => stats.constellation >= 4,
            text: "Additional AoE Damage",
            formulaText: stats => <span>222% {Stat.printStat(getTalentStatKey("burst", stats), stats)}</span>,
            formula: formula.burst.addDmg,
            variant: stats => getTalentStatKeyVariant("burst", stats),
          }, {
            canShow: stats => stats.constellation >= 4,
            text: "HP Recovered",
            formulaText: stats => <span>( 20% {Stat.printStat("finalHP", stats)} ) * {Stat.printStat("heal_multi", stats)}</span>,
            formula: formula.burst.regen,
            variant: "success"
          }, {
            text: "CD",
            value: "15s",
          }, {
            text: "Energy Cost",
            value: 60,
          },]
        }],
      },
      passive1: {
        name: "Stellar Predator",
        img: passive1,
        sections: [{ text: <span>When Fischl hits <b>Oz</b> with a fully-charged <b>Aimed Shot</b>, Oz brings down Thundering Retribution, dealing <span className="text-electro">AoE Electro DMG</span> equal to 152.7% of the arrow's DMG.</span> }],
      },
      passive2: {
        name: "Undone Be Thy Sinful Hex",
        img: passive2,
        sections: [{
          text: <span>If your active character triggers an <span className="text-electro">Electro-related Elemental Reaction</span> when Oz is on the field, the opponent shall be stricken with Thundering Retribution, dealing <span className="text-electro">Electro DMG</span> equal to 80% of Fischl's ATK.</span>,
          fields: [{
            canShow: stats => stats.ascension >= 4,
            text: "Thundering Retribution",
            formulaText: stats => <span>80% {Stat.printStat(getTalentStatKey("skill", stats), stats)}</span>,
            formula: formula.passive2.dmg,
            variant: stats => getTalentStatKeyVariant("skill", stats),
          }]
        }],
      },
      passive3: {
        name: "Mein Hausgarten",
        img: passive3,
        sections: [{ text: <span>When dispatched on an expedition in Mondstadt, the time consumed is reduced by 25%.</span> }],
      },
      constellation1: {
        name: "Gaze of the Deep",
        img: c1,
        sections: [{
          text: <span>Even when Oz is not present in combat, he can still watch over Fischl through the crow's eyes. When Fischl attacks an opponent, Oz fires a joint attack through the crow's eyes, dealing 22% of <span className="text-physical">ATK DMG</span>.</span>,
          fields: [{
            canShow: stats => stats.constellation >= 1,
            text: "Joint Attack DMG",
            formulaText: stats => <span>22% {Stat.printStat(getTalentStatKey("normal", stats), stats)}</span>,
            formula: formula.constellation1.dmg,
            variant: stats => getTalentStatKeyVariant("normal", stats),
          }]
        }],
      },
      constellation2: {
        name: "Devourer of All Sins",
        img: c2,
        sections: [{ text: <span>When <b>Nightrider</b> is used, it deals an additional 200% ATK as DMG, and its AoE is increased by 50%.</span> }],
      },
      constellation3: {
        name: "Wings of Nightmare",
        img: c3,
        sections: [{ text: <span>Increases <b>Nightrider</b>'s skill level by 3. Maximum upgrade level is 15.</span> }],
        stats: { skillBoost: 3 }
      },
      constellation4: {
        name: "Her Pilgrimage of Bleak",
        img: c4,
        sections: [{ text: <span>When <b>Midnight Phantasmagoria</b> is used, deals 222% of ATK as <span className="text-electro">Electro DMG</span> to surrounding opponents. When the skill duration ends, Fischl recovers 20% of her HP.</span> }],
      },
      constellation5: {
        name: "Against the Fleeing Light",
        img: c5,
        sections: [{ text: <span>Increases <b>Midnight Phantasmagoria</b>'s skill level by 3. Maximum upgrade level is 15.</span> }],
        stats: { burstBoost: 3 }
      },
      constellation6: {
        name: "Evernight Raven",
        img: c6,
        sections: [{ text: <span>Increases the duration of Oz's summoning by 2s. Additionally, Oz attacks with your active character when present, dealing 30% of Fischl's ATK as <span className="text-electro">Electro DMG</span>.</span> }],
      }
    },
  },
};
export default char;
